import React, { Component } from "react";
import { BsArrowLeft } from "react-icons/bs";
import { GetTrainingsListByStatusInterface } from "../../../../actions/training-offers";
import Button from "../../../../components/Buttons/Button";
import Criteria from "../../components/Criteria";
import { DetailsInfo } from "../../components/DetailsInfo";
import { Participants } from "./Participants";

enum TABS {
  PARTICIPANTS = "PARTICIPANTS",
  DETAILS = "DETAILS",
  CRITERIA = "CRITERIA",
}

interface TrainingDetailsProps {
  training_details: GetTrainingsListByStatusInterface;
  school_code: string;
  onClose: () => void;
}
interface TrainingDetailsState {
  selected_tab: TABS;
  loading: boolean;
  display_tabs: boolean;
}

export class TrainingDetails extends Component<
  TrainingDetailsProps,
  TrainingDetailsState
> {
  constructor(props: TrainingDetailsProps) {
    super(props);

    this.state = {
      loading: false,
      selected_tab: TABS.PARTICIPANTS,
      display_tabs: true,
    };
  }
  setDisplayTabs = (status: boolean) => this.setState({ display_tabs: status });
  render() {
    const details: GetTrainingsListByStatusInterface =
      this.props.training_details;
    return (
      <div>
        {/* Title */}
        {this.state.display_tabs === true && (
          <div className="flex flex-col lg:flex-row items-center justify-between gap-2 w-full borde-b border-primary-600 pb-5">
            <div className="flex flex-row items-center gap-2 w-full">
              <div>
                <div
                  onClick={() => {
                    this.state.loading === false && this.props.onClose();
                  }}
                  className="h-11 w-11 flex items-center justify-center rounded-full bg-accent-400 text-accent-900 hover:bg-accent-500 hover:text-accent-900 cursor-pointer"
                >
                  <BsArrowLeft className="text-2xl" />
                </div>
              </div>
              <div className="flex flex-col">
                <div className="font-bold text-xl text-gray-00">
                  {details.title}
                </div>
                {/* <div className="text-sm text-gray-500 font-normal">
                  {details.development_partner_name}
                </div> */}
              </div>
            </div>
            <div>
              <div className="flex flex-row items-center justify-end gap-0">
                {[TABS.PARTICIPANTS, TABS.DETAILS, TABS.CRITERIA].map(
                  (item, i) => (
                    <div
                      onClick={() => {
                        this.setState({
                          selected_tab: item,
                        });
                      }}
                      key={i + 1}
                      className={`rounded-t-md px-3 py-2 flex flex-row items-center gap-2 border-b-4 ${
                        item === this.state.selected_tab
                          ? "bg-white text-primary-700 border-primary-700 font-bold animate__animated animate__fadeIn"
                          : "hover:bg-primary-100 hover:border-primary-700  text-gray-700 hover:text-primary-700 bg-gray-10"
                      } cursor-pointer`}
                    >
                      <div className="truncate">
                        {item === TABS.PARTICIPANTS
                          ? "List of trainees"
                          : item === TABS.DETAILS
                          ? "Details"
                          : "Training criteria"}
                      </div>
                    </div>
                  )
                )}
              </div>
            </div>
          </div>
        )}
        {/* Details */}
        {this.state.selected_tab === TABS.PARTICIPANTS && (
          <Participants
            training={this.props.training_details}
            setDisplayTabs={this.setDisplayTabs}
            school_code={this.props.school_code}
          />
        )}
        {this.state.selected_tab === TABS.DETAILS && (
          <DetailsInfo
            training_details={this.props.training_details}
            onParticipants={() =>
              this.setState({ selected_tab: TABS.PARTICIPANTS })
            }
          />
        )}
        {this.state.selected_tab === TABS.CRITERIA && (
          <div>
            <div className="flex flex-row items-center justify-between gap-2 border-b border-gray-300 pb-3 mb-3">
              <div className="flex flex-row items-center gap-2">
                <div>
                  <div
                    onClick={() =>
                      this.setState({ selected_tab: TABS.DETAILS })
                    }
                    className="bg-accent-400 hover:bg-accent-500 text-accent-900 rounded px-3 py-2 font-bold cursor-pointer w-max flex flex-row items-center gap-2"
                  >
                    {/* <div>
                    <BsArrowLeft className="text-2xl" />
                  </div> */}
                    <div>Back to details</div>
                  </div>
                </div>
                <div>
                  <div className="text-lg font-bold text-black">
                    Training criteria
                  </div>
                  <div className="text-sm text-gray-500">
                    List of training criteria that the system consider while
                    choosing participants
                  </div>
                </div>
              </div>
              {/* Right side */}
              <div>
                <Button
                  onClick={() =>
                    this.setState({ selected_tab: TABS.PARTICIPANTS })
                  }
                  theme="accent"
                >
                  View trainees
                </Button>
              </div>
            </div>
            <Criteria
              trainee_criteria={this.props.training_details.trainee_criteria}
            />
          </div>
        )}
      </div>
    );
  }
}
