import React, { Component } from "react";
import { AiOutlineLoading3Quarters } from "react-icons/ai";
import { BsArrowLeft } from "react-icons/bs";
import { FaChalkboardTeacher, FaTimes } from "react-icons/fa";
import { IoMdInformationCircle } from "react-icons/io";
import { MdCheckCircle, MdClose } from "react-icons/md";
import {
  AssignTraineeToTraining,
  GetTrainingsListByStatusInterface,
} from "../../../../actions/training-offers";
import Button from "../../../../components/Buttons/Button";
import {
  HasTrainingStatus,
  SearchParentResponseInterface,
} from "./FormToAddParticipant";

interface TrainingParticipantDetailsProps {
  school_code: string;
  data: SearchParentResponseInterface;
  training_details: GetTrainingsListByStatusInterface;
  close: () => void;
  onSuccess: (data: SearchParentResponseInterface) => void;
  setLoading: (status: boolean) => void;
}
interface TrainingParticipantDetailsState {
  loading: boolean;
  error: string;
  success: string;
}

export class TrainingParticipantDetails extends Component<
  TrainingParticipantDetailsProps,
  TrainingParticipantDetailsState
> {
  constructor(props: TrainingParticipantDetailsProps) {
    super(props);

    this.state = {
      loading: false,
      error: "",
      success: "",
    };
  }
  returnStatusText = () => {
    if (this.props.data.hasTrainingStatus === HasTrainingStatus.AVAILABLE) {
      return "Available for training";
    }
    if (
      this.props.data.hasTrainingStatus === HasTrainingStatus.ALREADY_TRAINED
    ) {
      return "Already has trained";
    }
    if (this.props.data.hasTrainingStatus === HasTrainingStatus.HAS_TRAINING) {
      return "Has assigned to training";
    }
  };
  returnStatusComponent = () => {
    if (this.props.data.hasTrainingStatus === HasTrainingStatus.AVAILABLE) {
      return (
        <div className="bg-green-100 text-sm rounded-full font-bold text-green-700 border border-green-600 px-0 pr-2 py-0 w-max flex flex-row items-center gap-2">
          <div>
            <MdCheckCircle className="text-2xl" />
          </div>
          <span>{this.returnStatusText()}</span>
        </div>
      );
    }
    if (
      this.props.data.hasTrainingStatus === HasTrainingStatus.ALREADY_TRAINED
    ) {
      return (
        <div className="bg-red-100 text-sm rounded-full font-bold text-red-700 border border-red-600 px-0 pr-2 py-0 w-max flex flex-row items-center gap-2">
          <div>
            <FaTimes className="text-2xl" />
          </div>
          <span>{this.returnStatusText()}</span>
        </div>
      );
    }
    return (
      <div className="bg-yellow-100 text-sm rounded-full font-bold text-yellow-700 border border-yellow-600 px-0 pr-2 py-0 w-max flex flex-row items-center gap-2">
        <div>
          <IoMdInformationCircle className="text-2xl" />
        </div>
        <span>{this.returnStatusText()}</span>
      </div>
    );
  };
  //   Function to add participant in the database
  AddParticipant = () => {
    this.setState({ loading: true });
    this.props.setLoading(true);
    AssignTraineeToTraining(
      {
        district_code: null,
        level_id: "",
        position_code: "",
        qualification_id: "",
        school_code: parseInt(this.props.school_code),
        sector_id: null,
        trainee_request_id: parseInt(
          this.props.training_details.trainee_request_id
        ),
        training_id: this.props.training_details.id,
        training_to_group_id: parseInt(
          this.props.training_details.training_to_group_id
        ),
        user_id: this.props.data.user_id,
      },
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
        } | null
      ) => {
        // this.setState({ loading: loading });
        if (res !== null && res.type === "success") {
          // On Success
          //   this.props.onSuccess(this.props.data);
          this.setState({ loading: false });
          //   this.props.setLoading(false);
          this.setState({ success: res.msg });
        }
        if (res !== null && res.type === "error") {
          this.setState({
            error: res.msg,
            success: "",
            loading: false,
          });
        }
      }
    );
  };
  render() {
    return (
      <div>
        <div className="p-3 bg-primary-100 my-3 rounded-md animate__animated animate__fadeIn">
          <div className="flex flex-row items-center justify-between gap-2">
            <div className="flex flex-row items-center gap-2">
              {this.state.loading === false && (
                <div>
                  <div
                    onClick={this.props.close}
                    className="flex items-center justify-center h-10 w-10 rounded-full bg-white text-primary-700 border border-primary-300 hover:bg-accent-900 hover:text-white hover:border-white cursor-pointer"
                  >
                    <BsArrowLeft className="text-2xl" />
                  </div>
                </div>
              )}
              <div>
                {/* <div className="text-primary-800">Participant result:</div> */}
                <div className="text-lg font-bold text-primary-800">
                  {this.props.data.first_name} {this.props.data.last_name}
                </div>
                <div>
                  <this.returnStatusComponent />
                </div>
              </div>
            </div>
            {this.state.loading === false ? (
              this.state.success === "" && (
                <div className="flex flex-row items-center gap-2">
                  {this.props.data.hasTrainingStatus ===
                  HasTrainingStatus.AVAILABLE ? (
                    <Button
                      theme="success"
                      className="flex flex-row items-center gap-2 justify-center"
                      onClick={() => this.AddParticipant()}
                    >
                      <div>
                        <FaChalkboardTeacher className="text-xl" />
                      </div>
                      <span>Add participant</span>
                    </Button>
                  ) : (
                    <div
                      onClick={this.props.close}
                      className="w-max bg-white rounded px-3 py-2 font-bold cursor-pointer"
                    >
                      Go back
                    </div>
                  )}
                </div>
              )
            ) : (
              <div className="flex flex-row items-center gap-2 justify-center bg-white text-accent-900 font-bold px-3 py-2 rounded-md cursor-wait">
                <div>
                  <AiOutlineLoading3Quarters className="text-2xl animate-spin" />
                </div>
                <span className="animate__animated animate__fadeIn animate__infinite">
                  Adding participant, please wait...
                </span>
              </div>
            )}
          </div>
        </div>
        {this.state.error !== "" && (
          <div
            onClick={() => this.setState({ error: "", success: "" })}
            className="flex flex-row items-center justify-between font-bold text-lg gap-2 px-3 py-2 rounded-md bg-red-50 text-red-600 hover:bg-red-100 cursor-pointer my-2 border border-white animate__animated animate__shakeX"
          >
            <span>{this.state.error}</span>
            <div>
              <MdClose className="text-2xl" />
            </div>
          </div>
        )}
        {this.state.success !== "" && (
          <div
            // onClick={() => this.setState({ success: "", error: "" })}
            className="flex flex-row items-center justify-between font-bold text-lg gap-2 px-3 py-2 rounded-md bg-green-50 text-green-600 hover:bg-green-100 cursor-pointer my-2 border border-white"
          >
            <span>{this.state.success}</span>
            <div>
              <Button
                theme="success"
                onClick={() => {
                  this.props.onSuccess(this.props.data);
                  this.props.setLoading(false);
                }}
                className="text-base"
              >
                Continue
              </Button>
            </div>
          </div>
        )}
        <div className="animate__animated animate__fadeIn">
          <div className="flex flex-col border-b border-gray-300 pb-2 my-2">
            <span className="text-gray-500 font-bold">National ID</span>
            <span>{this.props.data.nid}</span>
          </div>
          <div className="flex flex-col border-b border-gray-300 pb-2 my-2">
            <span className="text-gray-500 font-bold">Phone number</span>
            <span>{this.props.data.phone_numbers}</span>
          </div>
          <div className="flex flex-col border-b border-gray-300 pb-2 my-2">
            <span className="text-gray-500 font-bold">Email</span>
            <span>{this.props.data.email}</span>
          </div>
          <div className="flex flex-col border-b border-gray-300 pb-2 my-2">
            <span className="text-gray-500 font-bold">Participant status</span>
            <span>{this.returnStatusText()}</span>
          </div>
        </div>
      </div>
    );
  }
}
