import React, { Component } from "react";
import { AiOutlineLoading3Quarters } from "react-icons/ai";
import { BsPersonPlus } from "react-icons/bs";
import { MdCheckCircle, MdClose, MdEdit } from "react-icons/md";
import {
  AddNewTraineeInfoAssignToTraining,
  GetTrainingsListByStatusInterface,
} from "../../../../actions/training-offers";
import Button from "../../../../components/Buttons/Button";
import TextInput from "../../../../components/Inputs/TextInput";
import { SearchParentResponseInterface } from "./FormToAddParticipant";

interface FormInputsToRegisterNewUserProps {
  training_details: GetTrainingsListByStatusInterface;
  nid: string;
  school_code: string;
  onSuccess: (data: SearchParentResponseInterface) => void;
  onClose: () => void;
  setLoading: (status: boolean) => void;
  editNID: () => void;
}
interface FormInputsToRegisterNewUserState {
  loading: boolean;
  error: {
    target:
      | "main"
      | "nid"
      | "first_name"
      | "middle_name"
      | "last_name"
      | "full_name"
      | "phone_number"
      | "email";
    msg: string;
  } | null;
  success: SearchParentResponseInterface | null;
  first_name: string;
  middle_name: string;
  last_name: string;
  full_name: string;
  phone_number: string;
  email: string;
}

export class FormInputsToRegisterNewUser extends Component<
  FormInputsToRegisterNewUserProps,
  FormInputsToRegisterNewUserState
> {
  constructor(props: FormInputsToRegisterNewUserProps) {
    super(props);

    this.state = {
      loading: false,
      error: null,
      success: null,
      email: "",
      first_name: "",
      full_name: "",
      last_name: "",
      middle_name: "",
      phone_number: "",
    };
  }
  submitData = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    this.setState({ success: null, error: null });
    // Validation
    if (this.state.first_name === "") {
      return this.setState({
        error: { target: "first_name", msg: "Please fill the first name" },
      });
    }
    if (this.state.last_name === "") {
      return this.setState({
        error: { target: "last_name", msg: "Please fill the last name" },
      });
    }
    if (this.state.phone_number === "") {
      return this.setState({
        error: { target: "phone_number", msg: "Please fill phone number" },
      });
    }
    if (this.state.email === "") {
      return this.setState({
        error: { target: "email", msg: "Please fill the email" },
      });
    }
    this.setState({ loading: true });
    this.props.setLoading(true);
    AddNewTraineeInfoAssignToTraining(
      {
        email: this.state.email,
        first_name: this.state.first_name,
        full_name: `${this.state.first_name}${" " + this.state.middle_name}${
          " " + this.state.last_name
        }`,
        last_name: this.state.last_name,
        middle_name: this.state.middle_name,
        nid: this.props.nid,
        phone_number: this.state.phone_number,
        school_code: this.props.school_code,
        trainee_request_id: this.props.training_details.trainee_request_id,
        training_id: this.props.training_details.id,
      },
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
          data: SearchParentResponseInterface | null;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.type === "success" && res.data !== null) {
          console.log("Test response: ", res.data);
          this.setState({
            success: res.data,
            error: null,
            loading: false,
          });
        }
        if (res !== null && res.type === "error") {
          this.setState({
            success: null,
            loading: false,
            error: { target: "main", msg: res.msg },
          });
          this.props.setLoading(false);
        }
      }
    );
  };
  render() {
    if (this.state.success !== null) {
      return (
        <div>
          <div className="bg-green-50 rounded-md flex flex-col items-center justify-center p-8 text-green-700 animate__animated animate__fadeIn animate__faster">
            <div>
              <MdCheckCircle className="text-7xl animate__animated animate__zoomIn" />
            </div>
            <div className="text-xl font-bold text-black">
              Trainee has been added successfully
            </div>
            <div>
              {`${this.state.first_name}${" " + this.state.middle_name}${
                " " + this.state.last_name
              }`}
            </div>
            <div className="text-sm text-black">
              This trainee has been added to the training, click the the
              following button to view the list
            </div>
            <Button
              onClick={() => {
                this.state.success !== null &&
                  this.props.onSuccess(this.state.success);
                this.props.setLoading(false);
              }}
              className="mt-5"
            >
              View trainees list
            </Button>
          </div>
        </div>
      );
    }
    return (
      <div className="animate__animated animate__fadeIn">
        {/* Selected National ID */}
        <div className="flex fle-row items-center justify-between gap-2 w-full bg-primary-100 px-2 py-2 pr-3 rounded-md my-4">
          <div className="flex flex-row items-center gap-3 w-full">
            <div>
              <div className="bg-white rounded-md p-2 text-primary-600">
                <BsPersonPlus className="text-3xl" />
              </div>
            </div>
            <div className="flex flex-col text-primary-800">
              <div className="text-sm font-light">
                Selected trainee National ID number
              </div>
              <div className="text-base bg-primary-100 w-max font-bold">
                NID: {this.props.nid}
              </div>
            </div>
          </div>
          <div>
            <div
              onClick={() => {
                if (
                  window.confirm(
                    "If you continue, you may loose your changes in the form. Are sure do you want to go back?"
                  ) === true &&
                  this.state.loading === false
                ) {
                  // Go back to edit
                  this.props.editNID();
                }
              }}
              className="bg-white hover:text-accent-900 rounded-md truncate flex flex-row items-center gap-2 justify-center p-2 cursor-pointer text-sm"
            >
              <div>
                <MdEdit className="text-2xl text-accent-900" />
              </div>
              <div>Edit National ID</div>
            </div>
          </div>
        </div>

        {/* Form for details */}

        <div>
          <form onSubmit={this.submitData} className="w-full my-3">
            <div className="grid grid-cols-12 gap-2">
              <TextInput
                onChange={(e) =>
                  this.setState({ first_name: e.target.value, error: null })
                }
                value={this.state.first_name}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "first_name"
                    ? this.state.error.msg
                    : ""
                }
                label={"First name (Kinyarwanda)"}
                placeholder={"Ex: KAMANA"}
                name={""}
                MainContainer_className="col-span-4"
                disabled={this.state.loading}
              />
              <TextInput
                onChange={(e) =>
                  this.setState({ middle_name: e.target.value, error: null })
                }
                value={this.state.middle_name}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "middle_name"
                    ? this.state.error.msg
                    : ""
                }
                label={"Middle name (Optional)"}
                placeholder={""}
                name={""}
                MainContainer_className="col-span-4"
                disabled={this.state.loading}
              />
              <TextInput
                onChange={(e) =>
                  this.setState({ last_name: e.target.value, error: null })
                }
                value={this.state.last_name}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "last_name"
                    ? this.state.error.msg
                    : ""
                }
                label={"Last name "}
                placeholder={"Ex: Damascene"}
                name={""}
                MainContainer_className="col-span-4"
                disabled={this.state.loading}
              />
              <TextInput
                onChange={(e) =>
                  this.setState({ phone_number: e.target.value, error: null })
                }
                value={this.state.phone_number}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "phone_number"
                    ? this.state.error.msg
                    : ""
                }
                label={"Phone number"}
                placeholder={"07XXXXXXXX"}
                name={""}
                MainContainer_className="col-span-6"
                disabled={this.state.loading}
              />
              <TextInput
                onChange={(e) =>
                  this.setState({ email: e.target.value, error: null })
                }
                value={this.state.email}
                error={
                  this.state.error !== null &&
                  this.state.error.target === "email"
                    ? this.state.error.msg
                    : ""
                }
                label={"Email"}
                placeholder={"email@gmail.com"}
                name={""}
                MainContainer_className="col-span-6"
                disabled={this.state.loading}
              />
            </div>
            {/* ----------------- */}
            {this.state.error !== null && this.state.error.target === "main" && (
              <div
                className="bg-red-50 text-red-700 flex flex-row items-center justify-between gap-3 px-3 py-2 cursor-pointer hover:bg-red-100 my-3 animate__animated animate__shakeX rounded-md"
                onClick={() => this.setState({ error: null, success: null })}
              >
                <span>{this.state.error.msg}</span>
                <div>
                  <MdClose className="text-2xl" />
                </div>
              </div>
            )}
            {this.state.success !== null && (
              <div
                className="bg-green-50 text-green-700 flex flex-row items-center justify-between gap-3 px-3 py-2 cursor-pointer hover:bg-green-100 my-3 animate__animated animate__fadeIn rounded-md"
                onClick={() => this.setState({ error: null, success: null })}
              >
                <span>{"Trainee has registered successfully!"}</span>
                <div>
                  <MdClose className="text-2xl" />
                </div>
              </div>
            )}
            {/* ----------------- */}
            <div className="flex flex-row items-center justify-between gap-2 mt-3 pt-3">
              {this.state.loading === false ? (
                <Button
                  onClick={this.props.onClose}
                  type="button"
                  theme="default"
                  disabled={this.state.loading}
                >
                  Cancel process
                </Button>
              ) : (
                <div className="animate__animated animate__fadeIn animate__infinite font-light text-accent-900">
                  {/* Loading, please wait... */}
                </div>
              )}

              <div className="flex flex-row items-center justify-end gap-3">
                {this.state.loading === true && (
                  <div>
                    <AiOutlineLoading3Quarters className="text-4xl text-accent-900 animate-spin" />
                  </div>
                )}
                <Button
                  type="submit"
                  theme="accent"
                  disabled={this.state.loading}
                >
                  {this.state.loading === true ? (
                    <span className="animate__animated animate__fadeIn animate__infinite">
                      Adding, please wait...
                    </span>
                  ) : (
                    "Submit trainee info"
                  )}
                </Button>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}
