import React, { Component } from "react";
import { AiOutlineLoading3Quarters } from "react-icons/ai";
import { FaChalkboardTeacher } from "react-icons/fa";
import { MdCheckCircleOutline, MdClose } from "react-icons/md";
import { RiSearchLine } from "react-icons/ri";
import {
  GetParentsToAttendTraining,
  GetTrainingsListByStatusInterface,
  TeachersToBeTrainedResponse,
  TrainingParticipants,
} from "../../../../actions/training-offers";
import Button from "../../../../components/Buttons/Button";
import { FormInputsToRegisterNewUser } from "./FormInputsToRegisterNewUser";
import { TrainingParticipantDetails } from "./TrainingParticipantDetails";

export enum HasTrainingStatus {
  AVAILABLE = "AVAILABLE",
  HAS_TRAINING = "HAS_TRAINING",
  ALREADY_TRAINED = "ALREADY_TRAINED",
}

export interface SearchParentResponseInterface {
  user_id: string;
  nid: string;
  first_name: string;
  last_name: string;
  phone_numbers: string;
  email: string;
  hasTrainingStatus: HasTrainingStatus;
}

interface FormToAddParticipantProps {
  school_code: string;
  training_details: GetTrainingsListByStatusInterface;
  onClose: () => void;
  addedParticipants: TrainingParticipants[];
  onSubmitParticipants: (participants: TeachersToBeTrainedResponse[]) => void;
  setLoading: (status: boolean) => void;
}
interface FormToAddParticipantState {
  loading: boolean;
  success: string;
  error: {
    target: "search_nid" | "main" | "first_name" | "last_name";
    msg: string;
  } | null;
  search_nid: string;
  user_id: string;
  first_name: string;
  last_name: string;
  phone_numbers: string;
  email: string;
  hasTraining: boolean;
  open_register: boolean;
  loaded_participant: SearchParentResponseInterface | null;
  open_register_user: string;
}

export class FormToAddParticipant extends Component<
  FormToAddParticipantProps,
  FormToAddParticipantState
> {
  constructor(props: FormToAddParticipantProps) {
    super(props);

    this.state = {
      loading: false,
      error: null,
      success: "",
      search_nid: "1199280217382112",
      user_id: "",
      first_name: "",
      last_name: "",
      phone_numbers: "",
      email: "",
      hasTraining: false,
      open_register: false,
      loaded_participant: null,
      open_register_user: "",
    };
  }
  GetParentToBeTrainedByNid = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (this.state.search_nid === "") {
      return this.setState({
        error: {
          target: "main",
          msg: "Please fill National ID the participant",
        },
      });
    }
    this.setState({ loading: true, error: null, success: "" });
    GetParentsToAttendTraining(
      {
        topic_id: this.props.training_details.id,
        sub_area_id: this.props.training_details.sub_area_id,
        nid: this.state.search_nid,
        start_date: this.props.training_details.start_date,
        end_date: this.props.training_details.end_date,
      },
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
          new: boolean;
          data: SearchParentResponseInterface | null;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.type === "success" && res.data !== null) {
          this.setState({
            loaded_participant: res.data,
            open_register: false,
            loading: false,
            open_register_user: "",
            success:
              "Participant found successfully! Now you can add the participant to the training",
          });
        }
        if (res !== null && res.type === "error") {
          this.setState({
            open_register: true,
            loading: false,
            hasTraining: false,
            error: {
              target: "main",
              msg: res.msg,
            },
            success: "",
            open_register_user: res.new === true ? this.state.search_nid : "",
          });
        }
      }
    );
  };

  //   Function to assign participant to the training
  AssignParticipantToTraining = (data: SearchParentResponseInterface) => {
    console.log("Testing one: ", data);
    this.props.onSubmitParticipants([
      {
        district_code: null,
        email: data.email,
        first_name: data.first_name,
        last_name: data.last_name,
        nid: data.nid,
        phone_numbers: data.phone_numbers,
        position_code: "",
        position_name: "",
        province_code: "",
        qualification_id: "",
        qualification_name: "",
        sector_code: null,
        subjects: [],
        teaching_level: "",
        teaching_level_id: 1,
        user_id: data.user_id,
        role: "",
        role_id: "",
      },
    ]);
    this.setState({ loaded_participant: null });
  };

  render() {
    if (this.state.open_register_user !== "") {
      return (
        <FormInputsToRegisterNewUser
          nid={this.state.open_register_user}
          onSuccess={(trainee: SearchParentResponseInterface) => {
            this.AssignParticipantToTraining(trainee);
            this.props.onClose();
          }}
          onClose={() => {
            this.setState({
              open_register_user: "",
              error: null,
              success: "",
              search_nid: "",
            });
          }}
          training_details={this.props.training_details}
          setLoading={this.props.setLoading}
          school_code={this.props.school_code}
          editNID={() =>
            this.setState({
              open_register_user: "",
              error: null,
              success: "",
            })
          }
        />
      );
    }
    return (
      <div>
        {this.state.loaded_participant === null && (
          <div className="bg-primary-100 rounded-md p-3 animate__animated animate__fadeIn">
            <div className="flex flex-row items-center gap-2 mb-2">
              <div>
                <RiSearchLine className="text-primary-800 text-2xl" />
              </div>
              <div className="text-primary-800 font-bold">
                Search for the trainee by NID
              </div>
            </div>
            <div className="text-sm text-primary-800 mb-2 font-light">
              Enter national ID for a person you want to attend this training
              <span className="font-normal">
                {" "}
                {this.props.training_details.title}
              </span>
            </div>
            {(this.props.training_details.total === null
              ? 0
              : this.props.training_details.total) >
            this.props.addedParticipants.length ? (
              <form
                onSubmit={this.GetParentToBeTrainedByNid}
                className="flex flex-row items-center gap-2 truncate"
              >
                {/* Search */}
                <div className="flex flex-col md:flex-row md:items-center justify-between w-full gap-2">
                  <div className="relative w-full">
                    <input
                      type="search"
                      className="fbg-white text-primary-800 w-full px-3 py-2 rounded border border-primary-700 font-normal"
                      placeholder="Type national ID number..."
                      value={this.state.search_nid}
                      onChange={(e) =>
                        this.setState({ search_nid: e.target.value })
                      }
                    />
                  </div>
                </div>
                <div>
                  <Button
                    type="submit"
                    theme="primary-light"
                    className="flex flex-row items-center justify-center gap-2 animate__animated animate__zoomIn bg-primary-700 text-white"
                  >
                    {this.state.loading === true && (
                      <div>
                        <AiOutlineLoading3Quarters className="text-2xl animate-spin text-white" />
                      </div>
                    )}
                    <span
                      className={`${
                        this.state.loading === true
                          ? "animate__animated animate__fadeIn animate__infinite"
                          : ""
                      } text-white`}
                    >
                      {this.state.loading === true
                        ? "Loading, wait..."
                        : "Search NID"}
                    </span>
                  </Button>
                </div>
              </form>
            ) : (
              <div>
                <div className="bg-white rounded-md mt-3 text-center p-10 animate__animated animate__zoomIn">
                  <div className="flex items-center justify-center">
                    <div className="relative">
                      <MdCheckCircleOutline className="text-6xl text-gray-400" />
                    </div>
                  </div>
                  <div className="font-bold text-xl">
                    All trainees has added
                  </div>
                  <div className="text-m text-gray-400">
                    All required trainees has added to the training, you can not
                    add another trainee
                  </div>
                </div>
              </div>
            )}
          </div>
        )}
        {/* Response */}
        {/* Error found */}
        {this.state.error !== null && this.state.error.target === "main" && (
          <div
            onClick={() => this.setState({ error: null })}
            className="bg-red-50 text-red-700  px-3 py-2 rounded-md cursor-pointer flex flex-row items-center justify-between gap-2 mt-3 animate__animated animate__shakeX"
          >
            <span>{this.state.error.msg}</span>
            <div>
              <MdClose className="text-2xl" />
            </div>
          </div>
        )}
        <div>
          {this.state.loaded_participant === null &&
            this.state.open_register === false &&
            (this.props.training_details.total === null
              ? 0
              : this.props.training_details.total) >
              this.props.addedParticipants.length && (
              <div className="bg-gray-100 rounded-md mt-3 text-center p-10">
                <div className="flex items-center justify-center">
                  <div className="relative">
                    <FaChalkboardTeacher className="text-6xl text-gray-400" />
                  </div>
                </div>
                <div className="font-bold text-xl">No trainee selected</div>
                <div className="text-m text-gray-400">
                  Search for a trainee to be added on the trainees list
                </div>
              </div>
            )}
        </div>
        {/* Response */}
        {this.state.loaded_participant !== null && (
          <TrainingParticipantDetails
            school_code={this.props.school_code}
            data={this.state.loaded_participant}
            close={() => this.setState({ loaded_participant: null })}
            setLoading={this.props.setLoading}
            onSuccess={(data: SearchParentResponseInterface) => {
              // Add this user to the list of added participants
              this.AssignParticipantToTraining(data);
            }}
            training_details={this.props.training_details}
          />
        )}
      </div>
    );
  }
}
