import React, { Component, Fragment } from "react";
import { AiOutlineLoading3Quarters } from "react-icons/ai";
import { BsJournalArrowDown } from "react-icons/bs";
import { FaUserEdit, FaUsers } from "react-icons/fa";
import { MdClose, MdInfo } from "react-icons/md";
import { RiSearchLine } from "react-icons/ri";
import {
  GetSchoolTrainingParticipants,
  GetTrainingsListByStatusInterface,
  RemoveTraineeToTraining,
  TeachersToBeTrainedResponse,
  TrainingParticipants,
} from "../../../../actions/training-offers";
import Button from "../../../../components/Buttons/Button";
import Loading from "../../../../components/Loading/Loading";
import { search } from "../../../../utils/functions";
import {
  AddParticipants,
  removeDuplicateParticipantsSubjects,
} from "./AddParticipants";

interface ParticipantsProps {
  training: GetTrainingsListByStatusInterface;
  school_code: string;
  setDisplayTabs: (status: boolean) => void;
}
interface ParticipantsState {
  loading: boolean;
  participants: TrainingParticipants[] | null;
  addParticipants: boolean;
  error: string;
  success: string;
  search_data: string;
  view_subjects: TrainingParticipants | null;
  removing_user_id: string | null;
}

export class Participants extends Component<
  ParticipantsProps,
  ParticipantsState
> {
  constructor(props: ParticipantsProps) {
    super(props);

    this.state = {
      loading: false,
      participants: null,
      addParticipants: false,
      error: "",
      success: "",
      search_data: "",
      view_subjects: null,
      removing_user_id: null,
    };
  }

  LoadParticipants = () => {
    this.setState({ loading: true });
    GetSchoolTrainingParticipants(
      this.props.training.id,
      this.props.school_code,
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
          data: TrainingParticipants[] | null;
        } | null
      ) => {
        this.setState({ loading: loading });
        if (res !== null && res.type === "success") {
          this.setState({ participants: res.data, loading: false });
        }
        if (res !== null && res.type === "error") {
          this.setState({ participants: [], loading: false, error: res.msg });
        }
      }
    );
  };

  RemoveParticipantOnTraining = (user_id: string, names: string) => {
    this.setState({ removing_user_id: user_id });
    RemoveTraineeToTraining(
      {
        training_id: this.props.training.id,
        user_id: user_id,
        status: "DISABLED",
      },
      (
        loading: boolean,
        res: {
          type: "success" | "error";
          msg: string;
        } | null
      ) => {
        if (res !== null && res.type === "success") {
          this.setState({
            participants:
              this.state.participants === null
                ? []
                : this.state.participants.filter(
                    (itm) => itm.user_id !== user_id
                  ),
            removing_user_id: null,
            success: `${names} has been removed from participants list successfully!`,
            error: "",
          });
        }
        if (res !== null && res.type === "error") {
          this.setState({
            removing_user_id: null,
            error: res.msg,
            success: "",
          });
        }
      }
    );
  };

  componentDidMount(): void {
    if (this.state.participants === null) {
      this.LoadParticipants();
    }
  }

  render() {
    if (this.state.loading === true || this.state.participants === null) {
      return (
        <div className="flex flex-col items-center justify-center p-6 bg-gray-100 rounded my-4">
          <Loading />
          <div className="text-center font-light text-lg">
            Loading, please wait...
          </div>
        </div>
      );
    }
    return (
      <Fragment>
        {this.state.addParticipants === false && (
          <div className="animate__animated animate__fadeIn">
            <div className="bg-gray-10 rounded-md">
              <div>
                {this.state.participants.length > 0 && (
                  <div className="flex flex-col lg:flex-row items-center justify-between gap-2 w-full">
                    <div className="flex flex-row items-center gap-4 w-full truncate">
                      <div>
                        <BsJournalArrowDown className="text-3xl text-accent-900" />
                      </div>
                      <div className="text-lg font-bold flex flex-col truncate">
                        <div>List of trainees</div>
                        <div
                          className="text-sm font-normal truncate"
                          title={`${this.props.training.development_partner_name}`}
                        >
                          {this.props.training.development_partner_name}
                        </div>
                      </div>
                    </div>
                    <div className="flex flex-col md:flex-row items-center justify-between gap-2 w-full">
                      <div className="relative w-full">
                        <div className="absolute px-3 py-2">
                          <RiSearchLine className="text-2xl text-gray-600" />
                        </div>
                        <input
                          type="search"
                          className="font-normal bg-gray-100 w-full px-3 pl-12 py-2 rounded"
                          placeholder="Search..."
                          value={this.state.search_data}
                          onChange={(e) =>
                            this.setState({ search_data: e.target.value })
                          }
                        />
                      </div>
                      <div className="flex flex-row items-center justify-between md:justify-end gap-2 w-full mt-2 md:mt-0">
                        <div
                          className={`${
                            this.state.participants.length ===
                            this.props.training.total
                              ? "text-green-600 bg-green-50 px-3 pt-1 rounded-md"
                              : "text-gray-900 bg-white px-3 pt-1 rounded-md"
                          } flex flex-col items-start justify-center truncate`}
                        >
                          <div className={`text-sm`}>Total trainees</div>
                          <div
                            className={`flex flex-row items-center gap-2 font-bold -mt-1 text-lg`}
                          >
                            <span>{this.state.participants.length}</span> of{" "}
                            <span>{this.props.training.total}</span>
                          </div>
                        </div>
                        <div>
                          {this.state.participants.length !==
                          this.props.training.total ? (
                            <Button
                              theme="accent"
                              className="animate__animated animate__zoomIn truncate flex flex-row items-center justify-center gap-2"
                              onClick={() => {
                                if (this.state.removing_user_id === null) {
                                  this.setState({ addParticipants: true });
                                  this.props.setDisplayTabs(false);
                                }
                              }}
                            >
                              <div>
                                <FaUserEdit className="text-2xl" />
                              </div>
                              <span>Add trainees</span>
                            </Button>
                          ) : (
                            // <Button
                            //   theme="success-light"
                            //   className="flex flex-row items-center justify-center gap-2 px-0 cursor-not-allowed"
                            // >
                            //   <div className="-ml-2">
                            //     <MdCheckCircle className="text-2xl" />
                            //   </div>
                            //   <span className="font-normal">Done</span>
                            // </Button>
                            ""
                          )}
                        </div>
                      </div>
                    </div>
                  </div>
                )}
              </div>
              {/* Body */}
              {this.state.participants.length === 0 && (
                <div>
                  <div className="bg-gray-100 text-center flex flex-col items-center justify-center p-3 py-8 rounded-lg mt-3">
                    <div className="text-center">
                      <FaUsers className="text-6xl text-gray-400 animate__animated animate__zoomIn" />
                    </div>
                    <div className="text-xl font-bold mt-2">
                      List of trainees
                    </div>
                    <div className="text-sm font-light">
                      You are required to add trainee from this training
                    </div>
                    <div className="mt-2">
                      <Button
                        theme="accent"
                        className="animate__animated animate__zoomIn flex flex-row items-center justify-center gap-2"
                        onClick={() => {
                          this.setState({
                            addParticipants: true,
                            success: "",
                            error: "",
                          });
                          this.props.setDisplayTabs(false);
                        }}
                      >
                        <div>
                          <FaUserEdit className="text-2xl" />
                        </div>
                        <span>Add trainees</span>
                      </Button>
                    </div>
                  </div>
                </div>
              )}
              {this.state.participants.length === this.props.training.total && (
                <div className="p-1 flex flex-row items-center gap-3 rounded-md bg-primary-100 my-3">
                  <div className="rounded-md h-14 w-14 flex items-center justify-center bg-white">
                    <MdInfo className="text-5xl text-primary-600 animate__animated animate__zoomIn" />
                  </div>
                  <div>
                    <div className="font-bold text-primary-800 text-lg">
                      Verify the selected trainees before the approval
                    </div>
                    <div className="text-sm text-primary-800">
                      Now you are able to make modification until the approval
                      will be made. make sure you have well verified the
                      selected trainees
                    </div>
                  </div>
                </div>
              )}
              {/* error */}
              {this.state.error !== "" && (
                <div
                  onClick={() => this.setState({ error: "", success: "" })}
                  className="bg-red-100 text-red-700 px-3 py-2 rounded cursor-pointer flex flex-row items-center justify-between gap-2 my-2 animate__animated animate__shakeX"
                >
                  <div className="flex flex-col">
                    <span className="text-lg font-bold">Error occurred:</span>
                    <span>{this.state.error}</span>
                  </div>
                  <div>
                    <MdClose className="text-2xl" />
                  </div>
                </div>
              )}
              {/* Success */}
              {this.state.success !== "" && (
                <div
                  onClick={() => this.setState({ error: "", success: "" })}
                  className="bg-green-100 text-green-700 px-3 py-2 rounded cursor-pointer flex flex-row items-center justify-between gap-2 my-2 animate__animated animate__fadeIn"
                >
                  <div className="flex flex-col">
                    <span className="text-lg font-bold">Done successfully</span>
                    <span>{this.state.success}</span>
                  </div>
                  <div>
                    <MdClose className="text-2xl" />
                  </div>
                </div>
              )}
              {/* List of participants */}
              {this.state.participants.length > 0 && (
                <div className="mt-3 w-full overflow-x-auto">
                  <table className="min-w-full text-left text-sm animate__animated animate__fadeIn">
                    <thead>
                      <tr className="text-primary-800 truncate">
                        <th className="px-2 py-2 border border-gray-300 text-center">
                          #
                        </th>
                        <th className="px-2 py-2 border border-gray-300">
                          Full name
                        </th>
                        <th className="px-2 py-2 border border-gray-300">
                          National ID
                        </th>
                        <th className="px-2 py-2 border border-gray-300">
                          Teaching level
                        </th>
                        <th className="px-2 py-2 border border-gray-300">
                          Qualification
                        </th>
                        <th className="px-2 py-2 border border-gray-300">
                          Position
                        </th>
                        <th className="px-2 py-2 border border-gray-300 text-right">
                          Subjects taught
                        </th>
                        <th className="px-2 py-2 border border-gray-300 text-right"></th>
                      </tr>
                    </thead>
                    <tbody>
                      {(
                        search(
                          this.state.participants,
                          this.state.search_data
                        ) as TrainingParticipants[]
                      ).length === 0 ? (
                        <tr>
                          <td
                            colSpan={8}
                            className="p-6 text-lg font-light text-center"
                          >
                            <div className="animate__animated animate__shakeX text-center">
                              No result found!
                            </div>
                          </td>
                        </tr>
                      ) : (
                        (
                          search(
                            this.state.participants,
                            this.state.search_data
                          ) as TrainingParticipants[]
                        ).map((item, i) => (
                          <tr key={i + 1} className="group">
                            <td className="px-2 py-2 border border-gray-300 text-center">
                              {i + 1}
                            </td>
                            <td className="px-2 py-2 border border-gray-300">
                              {item.first_name} {item.last_name}
                            </td>
                            <td className="px-2 py-2 border border-gray-300">
                              {item.nid}
                            </td>
                            <td className="px-2 py-2 border border-gray-300">
                              {item.teaching_level}
                            </td>
                            <td className="px-2 py-2 border border-gray-300">
                              {item.qualification_name}
                            </td>
                            <td className="px-2 py-2 border border-gray-300">
                              {item.position_name}
                            </td>
                            <td className="px-1 py-1 border border-gray-300">
                              {item.subjects.length > 0 ? (
                                <div
                                  onClick={() =>
                                    this.setState({ view_subjects: item })
                                  }
                                  className="bg-gray-100 px-2 py-2 rounded cursor-pointer hover:text-primary-800 text-sm w-max hover:bg-primary-100 float-right"
                                >
                                  View subjects (
                                  {
                                    removeDuplicateParticipantsSubjects(
                                      item.subjects
                                    ).length
                                  }
                                  )
                                </div>
                              ) : (
                                <div className="px-3">No subjects</div>
                              )}
                              {this.state.view_subjects !== null &&
                                this.state.view_subjects.nid === item.nid && (
                                  <div
                                    className="bg-white border rounded-md shadow-lg absolute right-5 animate__animated animate__zoomIn animate__faster"
                                    style={{ zIndex: 9 }}
                                  >
                                    <div className="flex flex-row items-center justify-between gap-2 p-2 px-3">
                                      <span className="font-bold text-lg">
                                        Subjects taught
                                      </span>
                                      <div>
                                        <div
                                          onClick={() =>
                                            this.setState({
                                              view_subjects: null,
                                            })
                                          }
                                          className="h-8 w-8 rounded-full bg-red-100 text-red-700 cursor-pointer hover:bg-red-200 flex items-center justify-center"
                                        >
                                          <MdClose className="text-2xl" />
                                        </div>
                                      </div>
                                    </div>
                                    {/* List */}
                                    <div className="p-2">
                                      <table className="w-full text-sm text-left">
                                        <thead>
                                          <td className="px-3 py-2 border border-gray-300 font-bold">
                                            #
                                          </td>
                                          <td className="px-3 py-2 border border-gray-300 font-bold">
                                            Course code
                                          </td>
                                          <td className="px-3 py-2 border border-gray-300 font-bold">
                                            Course name
                                          </td>
                                        </thead>
                                        <tbody>
                                          {removeDuplicateParticipantsSubjects(
                                            this.state.view_subjects.subjects
                                          ).map((course, i) => (
                                            <tr key={i + 1} className={``}>
                                              <td className="px-3 py-2 border border-gray-300">
                                                {i + 1}
                                              </td>
                                              <td className="px-3 py-2 border border-gray-300">
                                                {course.course_code}
                                              </td>
                                              <td className="px-3 py-2 border border-gray-300">
                                                {course.course_name}
                                              </td>
                                            </tr>
                                          ))}
                                        </tbody>
                                      </table>
                                    </div>
                                  </div>
                                )}
                            </td>
                            <td className="px-1 py-1 border border-gray-300">
                              <div
                                onClick={() => {
                                  if (this.state.removing_user_id === null) {
                                    if (
                                      window.confirm(
                                        `Are you sure do you want to remove ${item.first_name} ${item.last_name} from participants list?`
                                      ) === true
                                    ) {
                                      this.RemoveParticipantOnTraining(
                                        item.user_id,
                                        `${item.first_name} ${item.last_name}`
                                      );
                                    }
                                  }
                                }}
                                className={`flex items-center justify-center gap-2 rounded px-3 py-2 ${
                                  this.state.removing_user_id === item.user_id
                                    ? "bg-primary-100 text-primary-800 cursor-wait font-bold"
                                    : "bg-red-100 text-red-800 group-hover:bg-red-700 group-hover:text-white"
                                } text-sm w-max float-right cursor-pointer font-normal`}
                              >
                                {this.state.removing_user_id ===
                                  item.user_id && (
                                  <div>
                                    <AiOutlineLoading3Quarters className="text-xl animate-spin" />
                                  </div>
                                )}
                                <span>
                                  {this.state.removing_user_id === item.user_id
                                    ? "Removing..."
                                    : "Remove"}
                                </span>
                              </div>
                            </td>
                          </tr>
                        ))
                      )}
                    </tbody>
                  </table>
                </div>
              )}
            </div>
          </div>
        )}
        {this.state.addParticipants === true && (
          <AddParticipants
            onClose={() => {
              this.setState({ addParticipants: false });
              this.props.setDisplayTabs(true);
            }}
            onSubmitParticipants={(
              participants: TeachersToBeTrainedResponse[]
            ) => {
              // this.AddNewParticipants(participants[0]);
              this.setState({
                participants: [
                  ...(this.state.participants === null
                    ? []
                    : this.state.participants),
                  {
                    email: participants[0].email,
                    first_name: participants[0].first_name,
                    id: 1,
                    last_name: participants[0].last_name,
                    nid: participants[0].nid,
                    phone_numbers: participants[0].phone_numbers,
                    position_code: participants[0].position_name,
                    position_name: participants[0].position_name,
                    qualification_id: participants[0].qualification_id,
                    qualification_name: participants[0].qualification_name,
                    teaching_level: participants[0].teaching_level,
                    teaching_level_id: participants[0].teaching_level_id,
                    trainee_request_id: parseInt(
                      this.props.training.trainee_request_id
                    ),
                    training_id: this.props.training.id,
                    training_to_group_id: "",
                    user_id: participants[0].user_id,
                    subjects: participants[0].subjects,
                  },
                ],
              });
            }}
            addedParticipants={this.state.participants}
            school_code={this.props.school_code}
            training_details={this.props.training}
          />
        )}
      </Fragment>
    );
  }
}
